body {
  margin: 0px; }
  body #demo {
    padding: 20px;
    margin: auto;
    box-sizing: border-box;
    max-width: 500px;
    text-align: center; }
    body #demo #api-key-input {
      display: block;
      width: 100%;
      padding: 10px;
      margin: 20px 0px;
      box-sizing: border-box;
      border-radius: 5px;
      border: 1px solid lightgray; }
    body #demo .button {
      display: inline-block;
      margin-top: 0px;
      margin-left: 10px;
      padding: 10px 40px;
      background-color: #00e600;
      box-shadow: 0px 4px 0px 0px #00b300;
      font-family: sans-serif;
      border-radius: 10px;
      border-width: 0px;
      vertical-align: top;
      cursor: pointer; }
      body #demo .button:active {
        margin-top: 3px;
        box-shadow: 0px 1px 0px 0px #00b300; }
    body #demo #marker.button {
      background-color: #ff6666;
      box-shadow: 0px 4px 0px 0px #ff3333; }
      body #demo #marker.button:active {
        box-shadow: 0px 1px 0px 0px #ff3333; }
    body #demo #map {
      position: relative;
      margin: 10px auto;
      height: 250px;
      width: 100%;
      background-color: lightgray;
      border-radius: 5px;
      box-shadow: 1px 1px 1px lightgray; }
      body #demo #map::after {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-family: sans-serif;
        font-size: 21px;
        font-weight: bold;
        white-space: nowrap;
        color: gray; }
      body #demo #map.noAPIKey::after {
        content: "API key is missing"; }
      body #demo #map.loading::after {
        content: "Loading..."; }
